var swiperAbout = new Swiper(".about .mySwiper", {
    effect: "coverflow",
    grabCursor: true,
    centeredSlides: true,
    slidesPerView: "auto",
    loop: true,
    coverflowEffect: {
        rotate: 0,
        stretch: 0,
        depth: 100,
        modifier: 2,
        slideShadows: true,
    },
    navigation: {
        nextEl: '.about .swiper-button-next',
        prevEl: '.about .swiper-button-prev',
    },
});

$(document).ready(function() {

    // User
    $('.header-top__user').hover(function () {
        $('.header-top__user-hidden').toggleClass('header-top__user-hidden--active')
        $('.header-top__basket-hidden').removeClass('header-top__basket-hidden--active')
    })

    $(".header-top__basket").hover(onIn, onOut);
    $(".header-top__basket .header-top__basket-show").click(function() {
        if ($(this).parents(".header-top__basket").hasClass("header-top__basket--active")) {
            $(this).parents(".header-top__basket").removeClass("header-top__basket--active")
        } else {
            $(this).parents(".header-top__basket").addClass("header-top__basket--active")
        }
    })

    $('.header-top__basket-close').click(function () {
        $('.header-top__basket').removeClass('header-top__basket--active')
    })

    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()

    // Catalog
    $('.header-bottom__catalog-show').click(function () {
        $('.header-bottom__catalog').toggleClass('header-bottom__catalog--active')
        $(this).toggleClass('header-bottom__catalog-show--active')
        $(this).siblings('.header-bottom__catalog-hidden').toggleClass('header-bottom__catalog-hidden--active')
        if(/iPhone|iPad|iPod|Android/i.test(navigator.userAgent)){
            $('body').toggleClass('fixed-body')
        }
    })

    $('.header-bottom__item-show').click(function () {
        $(this).toggleClass('header-bottom__item-show--active')
        $(this).siblings('.header-bottom__item-hidden').toggleClass('header-bottom__item-hidden--active')
        $('.header-bottom__catalog-show').removeClass('header-bottom__catalog-show--active')
    })

    $('.header-bottom__item-close').click(function () {
        $('.header-bottom__item-hidden').removeClass('header-bottom__item-hidden--active')
        $('.header-bottom__catalog-show').addClass('header-bottom__catalog-show--active')
        $('.header-bottom__item-show').removeClass('header-bottom__item-show--active')
    })

    $('.header-bottom__item-show2').click(function () {
        $(this).toggleClass('header-bottom__item-show2--active')
        $(this).siblings('.header-bottom__item-hidden2').toggleClass('header-bottom__item-hidden2--active')
    })

    $('.header-bottom__item-close2').click(function () {
        $('.header-bottom__item-hidden2').removeClass('header-bottom__item-hidden2--active')
        $('.header-bottom__item-show2').removeClass('header-bottom__item-show2--active')
    })

    // Hover
    $('.advantages__info-show').hover(function () {
        $(this).siblings('.advantages__info-hidden').toggleClass('advantages__info-hidden--active')
    })

    // Delivery
    $('.delivery__form-btn').click(function () {
        $('.delivery__content').addClass('delivery__content--active')
        $('.delivery__content-hide').addClass('delivery__content-hide--active')
    })

    $('.delivery__hide-btn').click(function () {
        $('.delivery__content').removeClass('delivery__content--active')
        $('.delivery__content-hide').removeClass('delivery__content-hide--active')
    })

    // Favourites
    $('.recommendations-card__btn').click(function () {
        $(this).toggleClass('recommendations-card__btn--active')
    })

    $('.recommendations-card__btn2').click(function () {
        $(this).toggleClass('recommendations-card__btn2--active')
    })

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }

    // Up
    var btn = $('.fixed-btn__up');

    btn.on('click', function(e) {
        e.preventDefault();
        $('html, body').animate({scrollTop:0}, '300');
    });

    // Select
    $(".custom_select select").niceSelect()

    // Categories
    $('.catalog-categories__item-show').click(function () {
        $(this).toggleClass('catalog-categories__item-show--active')
        $(this).siblings('.catalog-categories__item-hidden').toggleClass('catalog-categories__item-hidden--active')
    })

    $('.catalog-categories__item-show2').click(function () {
        $(this).toggleClass('catalog-categories__item-show2--active')
        $(this).siblings('.catalog-categories__item-hidden2').toggleClass('catalog-categories__item-hidden2--active')
    })

    $('.catalog-categories__categories').click(function () {
        $('.catalog-categories__left').toggleClass('catalog-categories__left--active')
        $('body').toggleClass('fixed-body')
    })

    $('.catalog-categories__title-mob').click(function () {
        $('.catalog-categories__left').removeClass('catalog-categories__left--active')
        $('body').removeClass('fixed-body')
    })

    $('.catalog-categories__item-close').click(function () {
        $('.catalog-categories__item-show').removeClass('catalog-categories__item-show--active')
        $('.catalog-categories__item-hidden').removeClass('catalog-categories__item-hidden--active')
    })

    $('.catalog-categories__item-close2').click(function () {
        $('.catalog-categories__item-show2').removeClass('catalog-categories__item-show2--active')
        $('.catalog-categories__item-hidden2').removeClass('catalog-categories__item-hidden2--active')
    })
})

swiperAbout();

function onIn() {
    if (window.innerWidth > 767) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("header-top__basket--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 767) {
        $(this).removeClass("header-top__basket--active")
    }
}

